import React from 'react';
import styled from 'styled-components';
import FaCopyRight from 'react-icons/lib/fa/copyright';
import FooterCard from './FooterCard';

const FooterContainer = styled.div`
  background-color: #840124;
  text-align: center;
  color: #ffcece;
  padding: 8px 64px;

  > .footer-wrapper {
    display: inline-flex;
  }

  > .footer-cite {
    text-align: center;
    line-height: 2.6;
  }
`;

const Divider = styled.div`
  height: 1px;
  background-color: #650000;
`;

const CopyRight = styled.span`
  font-style: italic;
  font-size: 9px;
  vertical-align: middle;
  margin-left: 8px;
`;

const FooterCards = styled.div``;

const Footer = ({ footers }) => {
  return (
    <FooterContainer>
      <div className="footer-wrapper">
        <FooterCards>
          {footers.map((footer, index) =>
            <FooterCard
              key={index}
              {...footer}
            />
          )}
        </FooterCards>
      </div>
      <Divider />
      <div className="footer-cite">
         2017
        <CopyRight>
          <FaCopyRight />CopyRight Reservered
        </CopyRight>
      </div>
    </FooterContainer>
  );
};

export default Footer;



